.card {
	background: #fff;
	border-radius: 2px;
	box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	text-align: left;
	justify-content: center;
	margin: 10px 0 10px 0;
}

.card > .img-container {
	overflow: hidden;
	text-align: center;
	background: white;
	justify-content: center;
	margin: 20px;
	height: 250px;
}

.card > .img-container > img {
	width: 120px;
	height: 250px;
}
